@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@700;800&family=Fira+Sans:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    /* Colors */
    /* Greys */
    --grey100: #f5f5f5;
    --grey200: #eeeeee;
    --grey300: #e0e0e0;
    --grey400: #bdbdbd;
    --grey500: #9e9e9e;
    --grey600: #757575;
    --grey700: #616161;
    --grey800: #424242;
    --grey900: #212121;
    /* Alerts */
    --alertRed: #cb0200;
    --alertOrange: #e86c00;
    --alertGreen: #489141;
    /* Purple */
    --purpleLight: #8900e1;
    --purpleMain: #57068c;
    --purpleDark: #330662;
    --purpleBlack: #220337;
    /* Accents */
    --accentYellow: #ffc107;

    /* Fonts */
    --primaryFont: 'Fira Sans', sans-serif;
    --condensedFont: 'Fira Sans Condensed', sans-serif;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body{
  background-color: var(--grey100);
  font-family: var(--primaryFont);
}

nav{
  position: relative;
  width: 100%;
  min-height: 3.3rem;
}
  nav ul{
    list-style: none;
  }
  nav li{
    padding: 1vmin;
  }
  #title{
    font-size: 2rem;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 1rem;
  }
    #title a{
      text-decoration: none;  
      color: var(--grey800);
      font-weight: bold;
    }
  .icon{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
  }
    .icon img{
      height: 2.8rem;
      margin: 1.2vmin;
      float: right;
      padding: 0.5rem;
      border-radius: 100%;
    }
    .icon a{
      transition: 0.2s;
    }
    .icon a.active img{
      background-color: var(--grey300);
    }
#searchContainer{
    min-height: 60vh;
}
#departmentContainer{
    width: 100%;
    min-height: 50vh;
    border-top: 1rem solid var(--grey200);
    background-color: var(--grey300);
    position: relative;
}
    #departmentTitle{
        font-size: 1.7rem;
        padding: 1rem;
        font-weight: bold;
        color: var(--grey700);
        position: absolute;
        top: 0.3rem;
        left: 0.3rem;
        opacity: 0;
        transition: 1.2s;
    }
    #departments{
        opacity: 0;
        transition: 0.8s;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
        grid-gap: 1rem;
        padding: 6rem 2rem 2rem 2rem;
    }
    .school{
        padding: 1rem;
    }
        .schoolTitle{
            font-size: 1.2rem;
            font-family: var(--condensedFont);
            text-align: center;
            margin: 1rem 0;
        }
            .schoolCode{
                padding: 0.3rem;
                color: var(--grey600);
                font-weight: 800;
            }
            .schoolName{
                color: var(--grey900);
            }
        .department{
            margin: 0.3rem 0;
        }
            .departmentCode{
                color: var(--grey600);
                font-family: var(--condensedFont);
                font-weight: 700;
            }
            .departmentName{
                
            }
#searchBar{
    position: absolute;
    top: 10vmax;
    left: 10vmin;
    width: 80vmin;
    height: 4rem;
    background-color: white;
    padding: 1.2rem 2rem;
    font-size: 1.3rem;
    cursor: text;
    outline: solid;
    outline-color: var(--grey300);
    transition: 0.2s;
}
    #searchBar:focus{
        outline-color: var(--purpleMain);
    }
#searchBar[placeholder]:empty:before {
    content: attr(placeholder);
    color: rgb(136, 136, 136); 
    font-size: 1.2rem;
}
#searchResults{
    position: absolute;
    top: calc(10vmax + 5rem);
    left: 10vmin;
}

